:root {
  --bg: #f6f0e5;
  --canvas: #F0E8D8;
  --ink: #3a2f26;
  --muted-ink: #6e5f53;

  --ui-100: #efe5d5;
  --ui-200: #e3d6c1;
  --ui-300: #d4c2a5;

  --accent: #f1c978;
  /* butter yellow */
  --main-title: #8B5A2B;
  --main-header: #f3da9a;
  /* saddle brown */
  --accent-ink: #3D2817;

  --danger: #E8A8A8;

  --radius: 8px;
  --border: 1.5px;
  --shadow: 3px 3px 0 #3a2f26;
  --shadow-soft: 2px 2px 0 rgba(58, 47, 38, 0.2);

  --font-ui: ui-rounded, "Nunito", system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
  --font-pixel: "Press Start 2P", monospace;

  /* Element specific */
  --button: #F4D58D;
  --button-hover: #ecce88;
  --button-shadow: -2px -2px 0 0 rgba(0, 0, 0, 0.25) inset, 2px 2px 0 0 rgba(255, 255, 255, 0.25) inset, 3px 3px 0 0 rgba(0, 0, 0, 0.30);
  --button-border: 2px solid var(--accent-ink, #3D2817);

  --readme-content: #8B6F47;

  --category-pill-background: #E8D4A0;
}

@font-face {
  font-family: 'Press Start 2P';
  src: url('./fonts/press_p2_start/PressStart2P-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'DotGothic16';
  src: url('./fonts/dotgothic16/DotGothic16-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  background: var(--bg);
  color: var(--ink);
  font-family: 'DotGothic16', monospace;
  font-family: 'consolas';
}

body {
  margin: 0;
  font-family: var(--font-ui);
  -webkit-font-smoothing: antialiased;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  font-family: inherit;
  cursor: pointer;
}

/* Centered app "window" for web; Electron will be the OS window */
.pixel-app-shell {
  min-height: 100%;
  display: grid;
  place-items: center;
  /* padding: 16px; */
}

.pixel-window {
  width: 300px;
  height: 400px;
  background: var(--canvas);
  border-radius: var(--radius);
  border: var(--border) solid var(--ink);
  box-shadow: var(--shadow);
  display: grid;
  grid-template-rows: auto 1fr;
  overflow: hidden;
}

/* Retro title bar */
.pixel-titlebar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--main-header, #E8D4A0);
  border-bottom: var(--border) solid var(--ink);
  padding: 8px 10px;

  font-family: 'Press Start 2P', monospace;
  font-size: 9px;
  color: var(--main-title, #8B5A2B);
}

.pixel-title {
  font-weight: 800;
  letter-spacing: 0.5px;
}

.pixel-close {
  width: 18px;
  height: 18px;
  border: var(--border) solid var(--ink);
  background: #eac1c6;
  display: grid;
  place-items: center;
  border-radius: 4px;
  box-shadow: var(--shadow-soft);
}

.pixel-content {
  overflow: auto;
  height: 100%;
}

/* Generic sections */
.page-container {
  padding: 12px 8px;
}

/* Buttons */
.pixel-button {
  display: flex;
  padding: 12px 18px;
  justify-content: center;
  align-items: center;
  gap: 10px;

  background: var(--button, #F4D58D);
  color: var(--accent-ink);
  text-align: center;
  font-size: 14px;

  /* border-radius: 6px; */
  border: var(--button-border, 2px solid var(--accent-ink, #3D2817));
  box-shadow: var(--button-shadow, -2px -2px 0 0 rgba(0, 0, 0, 0.25) inset, 2px 2px 0 0 rgba(255, 255, 255, 0.25) inset, 3px 3px 0 0 rgba(0, 0, 0, 0.30));
  transition: transform 80ms ease;
}

.pixel-button:hover {
  /* transform: translate(-1px, -1px); */
  background: var(--button-hover, #ecce88);
}

.pixel-button:active {
  transform: translate(1px, 1px);
  box-shadow: 1px 1px 0 var(--ink);
}

.pixel-button-ghost {
  background: transparent;
  border: var(--border) solid var(--ink);
}

/* Landing */
.landing-wrap {
  height: 100%;
  display: grid;
  place-items: center;
}

.landing-inner {
  display: -webkit-flex;
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;
  row-gap: 22px;
}

.landing-title {
  font-size: 16px;

  display: -webkit-flex;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  row-gap: 2px;
}

.new-message-bubble {
  display: none;
}

.unread .new-message-bubble {
  display: block;
  position: relative;
  width: 120px;
  height: 20px;
}

.unread .new-message-bubble .bubble-background {
  position: absolute;
  top: -12px;
  left: 50%;
  z-index: 0;
  width: 150px;
  transform: translateX(-50%);
}

.unread .new-message-bubble .bubble-text {
  position: absolute;
  z-index: 1;
  font-size: 10px;
  top: -7px;
  left: 0;

  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  border-right: 2px solid #3a2f26;

  width: 0;
  animation:
    typingLoop 10s steps(25, end) infinite,
    blink 0.75s step-end infinite;
}

/* Type → hold → instant reset */
@keyframes typingLoop {
  0% {
    width: 0;
  }

  40% {
    width: 100%;
  }

  /* typing ends */
  84.999% {
    width: 100%;
  }

  /* hold fully visible */
  85% {
    width: 0;
  }

  /* ⟵ snap instantly back */
  100% {
    width: 0;
  }

  /* stay collapsed until next loop */
}

/* Cursor blink */
@keyframes blink {
  50% {
    border-color: transparent;
  }
}


/* === SPRITE ANIMATION === */
.sprite {
  display: inline-block;
}

.unread .sprite {
  animation: spriteJump 10s infinite;
}

@keyframes spriteJump {

  0%,
  39% {
    transform: translateY(0);
  }

  /* normal during typing */
  40% {
    transform: translateY(-6px);
  }

  /* jump right when typing ends */
  45%,
  100% {
    transform: translateY(0);
  }

  /* land and stay normal */
}

.landing-btn-container {
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.landing-subcopy {
  color: var(--muted-ink);
  font-size: 12px;
}

/* Readme */

.return-home-btn {
  margin: 10px 8px 0px -12px;
  width: 120px;
  scale: 0.8;
}

.readme-heading {
  text-align: center;
  margin-bottom: 12px;
}

.readme-heading * {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}

.readme-card {
  background: #fffdf8;
  border: var(--border) solid var(--ink);
  padding: 10px;
  margin-bottom: 10px;
}

.readme-block-title {
  display: flex;
  align-items: center;
  flex-direction: row;

  gap: 6px;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  margin-bottom: 6px;
  color: var(--accent-ink, #3D2817);
}

.readme-block-content {
  color: var(--readme-content, #8B6F47);
  font-size: 10px;
  line-height: 18px;
  font-weight: 400;

  padding: 4px;
}

.readme-block-icon {
  display: -webkit-inline-box;
  display: inline-flex;

  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  padding: 2px;

  color: var(--accent-ink, #3D2817);
}

.readme-block-icon svg * {
  fill: #3D2817;
}

/* Header & category pills */
.app-header {
  display: -webkit-flex;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  flex-direction: row;
  column-gap: 4px;

  font-size: 12px;
  margin-bottom: 12px;
}

.app-header a {
  text-decoration: underline;
}

.app-header a:hover {
  font-weight: bold;
}

.category-row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2px;

  margin-bottom: 8px;
  grid-auto-rows: 30px;
}

.category-pill {
  padding: 2px 8px;

  border: 1px solid var(--accent-ink, #3D2817);
  background: var(--category-pill-background, #E8D4A0);
  box-shadow: -2px -2px 0 0 rgba(0, 0, 0, 0.20) inset, 2px 2px 0 0 rgba(255, 255, 255, 0.30) inset;

  text-align: left;
  font-size: 12px;
  filter: brightness(1);
}

.category-pill:hover {
  filter: brightness(0.9);
}

.category-pill:active {
  transform: translate(1px, 1px);
}

.category-pill.active {
  background-color: #D4C4A8;
  box-shadow: var(--shadow-soft);
}

/* when there is unread */
.category-pill.has-unread {
  border-color: var(--ink);
  box-shadow: 0 0 0 2px rgba(58,47,38,0.08) inset;
}

/* small numeric badge (top-right) */
.pill-badge {
    position: absolute;
    top: -4px;
    right: -2px;
    min-width: 16px;
    height: 16px;
    padding: 0 4px;
    border-radius: 10px;
    background: #d26b6b;
    color: #fffdf8;
    border: var(--border) solid var(--ink);
    font-size: 10px;
    line-height: 14px;
    text-align: center;
    scale: 0.9;
}

/* dot variant (if showCounts=false) */
.pill-dot {
  position: absolute;
  top: -4px;
  right: -4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #d26b6b;
  border: var(--border) solid var(--ink);
}

/* Chat */
.chat-page .page-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.chat-feed {
  flex: 1;

  display: grid;
  gap: 10px;
  padding: 12px 10px;
  background-color: #f6f1e8;
  margin-left: -8px;
  margin-right: -8px;
  border-top: 1px solid #3D2817;
  border-bottom: 1px solid #3D2817;
}

.chat-feed.empty {
  background-color: transparent;
  border: none;
}

.chat-bubble-wrapper {
  max-width: 240px;
}

.chat-bubble {
  background: #fffdf8;
  border: var(--border) solid var(--ink);
  padding: 10px;
  position: relative;

  font-size: 12px;
  margin-left: 10px;
}

.chat-bubble::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -11px;
  width: 0;
  height: 0;
  border-left: 11px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 10px solid var(--ink);
  transform: rotate(270deg);
}

.chat-bubble::after {
  content: "";
  position: absolute;
  top: 0.9px;
  left: -9px;
  width: 0;
  height: 0;
  border-left: 11px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 9px solid #fffdf8;
  transform: rotate(270deg);
}

.chat-bubble a {
  word-break: break-all;
}

.bubble-title {
  font-weight: 800;
  margin-bottom: 6px;
  text-transform: capitalize;
}

.bubble-meta {
  font-size: 11px;
  color: var(--muted-ink);
  margin-top: 12px;
  border-top: solid var(--muted-ink) 1px;
  padding-top: 6px;
  padding-left: 6px;
  margin-left: -4px;
  margin-right: -4px;
  padding-bottom: 2px;
}

.bubble-actions {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}

/* Bottom dock */
.bottom-dock {
  position: sticky;
  bottom: 0;
  background: linear-gradient(180deg, rgba(243, 235, 223, 0) 0%, var(--canvas) 40%);
  padding-top: 6px;
  padding-bottom: 6px;
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 8px;
  align-items: end;
}

.pixel-button.shuffle-btn {
  background-color: var(--canvas, #F0E8D9);
}

/* Modal */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(58, 47, 38, 0.25);
  display: grid;
  place-items: center;
}

.modal-card {
  background: var(--canvas);
  border: var(--border) solid var(--ink);
  border-radius: 8px;
  width: 92%;
  max-width: 280px;
  padding: 10px;
}

.modal-title {
  font-weight: 800;
  margin-bottom: 8px;
}

.modal-actions {
  display: flex;
  gap: 8px;
  justify-content: flex-end;
  margin-top: 8px;
}

.textarea {
  width: 100%;
  min-height: 80px;
  resize: vertical;
  background: #fffdf8;
  border: var(--border) solid var(--ink);
  border-radius: 6px;
  padding: 8px;
}

.input {
  width: 100%;
  background: #fffdf8;
  border: var(--border) solid var(--ink);
  border-radius: 6px;
  padding: 6px 8px;
  margin-top: 6px;
}

.app-drag-region {
  -webkit-app-region: drag;
}

.app-no-drag {
  -webkit-app-region: no-drag;
}

.window-controls {
  display: flex;
  gap: 6px;
}

.ctrl-btn {
  width: 22px;
  height: 22px;
  line-height: 1;
  border: var(--border) solid var(--ink);
  background: var(--ui-200);

  font-size: 12px;
}

.ctrl-btn:hover {
  filter: brightness(0.9);
}

.ctrl-btn.minimize {
  background: var(--ui-100);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 4.5px;
}

.ctrl-btn.danger {
  background: var(--danger, #E8A8A8);
  display: flex;
  justify-content: center;
  align-items: baseline;
  font-weight: bold;

  padding-top: 2px;
}